@media screen and (max-width: 1366px) {
    .grid-40 {
        .row {
            margin-left: -15px;
            margin-right: -15px;
        }

        [class='col'] {
            padding-left: 10px;
            padding-right: 10px;
            flex: 1 0 25%;
            max-width: 25%;
        }
    }

    .bm {
        &-plan-item {
            .container-radio {
                display: block;
                position: relative;
                padding-left: 35px;
                margin-bottom: 12px;
                cursor: pointer;
                font-size: 22px;
                user-select: none;

                input {
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;
                }

                .checkmark {
                    position: absolute;
                    top: -14px;
                    left: 0;
                    height: 40px;
                    width: 40px;
                    border-radius: 50%;
                    border: 3px solid #7c7c7c;

                    &::after {
                        content: "";
                        position: absolute;
                        display: none;
                        top: 7px;
                        left: 7px;
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        background: white;
                    }
                }

                input:checked~.checkmark {
                    border-color: $white;
                }

                input:checked~.checkmark:after {
                    display: block;
                }
            }

            &.selected-plan {
                .card-body {
                    min-height: 500px;
                    background-color: $accent-secondary;
                }

                .card-header {
                    background: $accent-primary;
                    color: $white;
                    height: 127px;

                    .text-header {
                        margin-left: 20px;
                        font-size: 36px;
                    }
                }

                .month {
                    width: 100%;
                    height: 136px;
                    background-color: $white;

                    .text-month {
                        font-size: 36px;
                        line-height: 40px;
                    }
                }

                .list-text {
                    display: inline-block;
                    text-align: left;
                    margin: 20px 0;
                    font-size: 30px;
                    line-height: 35px;

                    li {
                        &:not(:last-child) {
                            margin-bottom: 16px;
                        }
                    }
                }

                .wrapper-text {
                    font-size: 30px;
                    line-height: 35px;
                }
            }

            .month {
                width: 327px;
                height: 113px;
                background-color: $accent-secondary;
                box-shadow: 0px 2px 6px rgba(77, 78, 78, 0.2);
                border-radius: 10px;
                margin: 0 auto;
                padding: 20px;
                @include flex;
                flex-direction: column;

                .text-month {
                    font-size: 32px;
                }

                small {
                    margin-top: 10px;
                    display: inline-block;
                }
            }

            .list-text {
                display: inline-block;
                text-align: left;
                margin: 20px 0;
                font-size: 24px;
                line-height: 28px;
            }

            .card-header {
                background: $accent-secondary;
                border-radius: 5px;
                border-color: $accent-primary;
                height: 122px;
                @include flex;

                .text-header {
                    margin-left: 20px;
                    font-size: 32px;
                }
            }

            .card-body {
                padding: 40px;
                min-height: 421px;
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
    }
}

