    .um-event {
        padding-left: 0;

        .event-remove {
            position: absolute;
            right: 32px;
            bottom: 20px;
        }
    }

    #reviewModal {
        .list-rating {
            margin: 24px 0;
        }

        .modal-footer {
            border: 0;
            padding-top: 0;
            padding-bottom: 24px;
            padding-right: 30px;
        }
    }

    .ck-content {
        min-height: 200px;
    }

    .dropdown-user {
        a:hover {
            color: $text-color;
        }

        .drop-avatar {
            margin-right: 10px;
            width: 60px;
            height: 60px;
        }

        .drop-user-info {
            .page-text-lg {
                margin-bottom: 8px;

                +span {
                    border-bottom: 1px solid $text-color;
                }
            }
        }

        .dropdown-menu[data-bs-popper] {
            width: 320px;
            height: auto;
            margin-left: 0;
            margin-top: 5px;
            padding: 16px;

            .btn {
                max-width: 276px;
                margin: 0 auto;
            }

        }

        .user-info-btn {
            margin-top: 16px;

            .btn+.btn {
                margin-top: 16px;
            }
        }
    }

    .owl-nav {
        img {
            max-height: 32px;
        }
    }

    .list-comment,
    .reservation-table,
    .um-coupon-list {
        margin-top: 0;
    }

    .toast {
        &-container {
            left: 0;
            bottom: 16px;
        }
    }

    .page-um-change-pass .um-top,
    .page-um-general .um-top {
        margin-bottom: 0;
    }

    .um-change-password {
        padding: 50px 32px;

        .page-title-md {
            margin-bottom: 30px;
        }
    }

    .icon-show-pass {
        top: 13px;
    }

    .bm {
        &-plan-success {
            margin: 80px 0 100px;

            h3 {
                color: $accent-primary;
            }

            p {
                margin: 30px 0;
                padding: 0;
            }
        }

        &-list {
            &-empty {
                padding: 0;

                h3 {
                    margin-bottom: 30px;
                }
            }
        }

        &-plan-top {
            margin-top: 0;

            .paypal {
                margin: 16px 0 8px;
            }

            .switch-btn {
                margin: 24px 0;
                margin-left: 10px;
            }
        }

        &-plan-trail {
            margin-top: 0;
        }

        &-plan-item {
            border: 1px solid $accent-primary;
            border-radius: 5px;
            transition: all .2s ease;

            .container-radio {
                .checkmark {
                    position: absolute;
                    top: -10px;
                    left: 0;
                    height: 30px;
                    width: 30px;

                    &::after {
                        content: "";
                        position: absolute;
                        display: none;
                        top: 4px;
                        left: 4px;
                        width: 16px;
                        height: 16px;
                        border-radius: 50%;
                        background: white;
                    }
                }

                input:checked~.checkmark {
                    border-color: $white;
                }

                input:checked~.checkmark:after {
                    display: block;
                }
            }

            &.selected-plan {
                .card-body {
                    min-height: 100%;
                    background-color: $accent-secondary;
                }

                .card-header {
                    background: $accent-primary;
                    color: $white;
                    height: 80px;

                    .text-header {
                        margin-left: 10px;
                        font-size: 28px;
                    }
                }

                .month {
                    width: 100%;
                    height: 100px;
                    background-color: $white;

                    .text-month {
                        font-size: 30px;
                        line-height: 36px;
                    }
                }

                .list-text {
                    font-size: 16px;
                    line-height: 20px;

                    li {
                        &:not(:last-child) {
                            margin-bottom: 5px;
                        }
                    }
                }

                .wrapper-text {
                    font-size: 20px;
                    line-height: 24px;
                }
            }

            .month {
                height: 80px;
                padding: 10px;

                .text-month {
                    font-size: 32px;
                }

                small {
                    margin-top: 10px;
                    display: inline-block;
                }
            }

            .list-text {
                display: inline-block;
                text-align: left;
                margin: 16px 0;
                font-size: 16px;
                line-height: 20px;
            }

            .card-header {
                height: 60px;
                @include flex;

                .text-header {
                    margin-left: 20px;
                    font-size: 24px;
                }
            }

            .card-body {
                padding: 20px;
                min-height: 100%;
            }
        }

        &-content .content-top h2 {
            margin-bottom: 10px;
        }

        &-reservation {
            padding-left: 0;

            .reservation-table {
                tr {
                    td {
                        padding: 15px;
                        min-width: 80px;

                        &:nth-child(2) {
                            word-break: unset;
                        }

                        &:nth-child(5) {
                            min-width: 100px;
                        }

                        &:nth-child(6) {
                            min-width: 130px;
                        }
                    }
                }
            }
        }
    }

    .page-business-manager {
        padding: 45px 0;
    }

    #bmCannotCreateModal {
        .modal-dialog {
            p {
                margin-bottom: 30px;
            }
        }
    }

    .page-header {
        border-bottom: 2px solid #C20000;
    }

    .bm-coupon-detail .detail-right .coupon-state {
        margin: 30px 0;
    }

    .bm-coupon-modal .modal-footer {
        margin-top: 30px;
    }

    .bm-modal-config {
        .modal-body {
            padding: 40px;
        }

        .apply-everyday,
        .text-config {
            margin-bottom: 30px;
        }

        .wrapper-config {
            margin: 30px 0;
        }

        .wrapper-input {
            width: 250px;

            .square-input {
                width: 64px;
            }
        }


        .weekdays-selector input[type=checkbox]+label {
            display: inline-block;
            width: 60px;
            height: 44px;
            line-height: 44px;
            border: 1px solid $accent-primary;
            background: $accent-secondary;
            text-align: center;
            cursor: pointer;
            color: $text-color;
            transition: all .25s ease;
            font-weight: 500;
        }
    }
}

@media screen and (max-width: 576px) {
    .grid-60 [class*='col-'] {
        padding-left: 8px;
        padding-right: 8px;
    }

    .bm-coupon-detail .detail-right .coupon-state-item {
        min-width: 80px;
    }

    .bm-coupon-detail .detail-actions {
        margin-top: 20px;
    }

    .grid-40 {
        [class='col'] {
            &:last-child {
                img {
                    max-height: 40px;
                }
            }
        }

        .bm-item {
            margin-bottom: 20px;

            &-img {
                padding: 10px;
            }

            &-add {
                height: calc(100% - 20px);
                min-height: 320px;
            }
        }
    }

    .search-box {
        .form-control {
            padding: 7px 34px;
            font-size: 18px;
            line-height: 20px;
        }

        &-icon {
            left: 8px;
            top: 7px;

            img {
                max-height: 19px;
            }
        }
    }

    .pagination-left {
        margin: 16px 0;
    }

    .page-item {
        .page-link {
            font-size: $fs18;
            line-height: 20px;
        }
    }

    .signin-right {
        margin: 50px 0;
    }

    .page-notification {
        padding: 40px 0 60px;
    }

    .mb-100 {
        margin-bottom: 30px;
    }

    .page-signup {
        margin: 50px 0;
    }

    .search-box {
        width: 100%;
    }

    .page-notification {
        padding: 40px 0;
    }

    .notification {
        &-content {
            padding: 16px;
        }

        &-item {
            padding: 8px 16px;
        }

        &-sort {
            img {
                max-width: 24px;
            }

            .custom-select {
                margin-top: 8px;
                max-width: 120px;
            }
        }
    }

    .customer-event {
        padding: 40px 0;

        &-top {
            height: 52px;
        }

        &-left {
            .page-heading {
                margin-bottom: 16px;
            }
        }

        &-list {
            padding: 16px 0;
        }

        .zero-event {
            height: 300px;
            padding-top: 50px;
        }
    }

    .zero-box {
        height: 300px;
        padding: 50px 20px 20px;

        img {
            max-width: 100px;
        }

        p {
            margin-top: 30px;
        }
    }

    .bp-reservation {
        &-zero {
            height: 400px;
        }
    }

    .customer-event-item {
        padding: 16px;

        .event-img {
            margin-bottom: 16px;
        }

        .event-join {
            position: relative;
            right: 0;
            bottom: 0;
        }
    }

    .customer-service {
        &-content {
            h2 {
                margin-bottom: 30px;
            }
        }

        &-list {
            padding: 40px 0;

            .service-list-top {
                margin-bottom: 24px;
            }

            .zero-service {
                background-color: $bg-secondary;
                padding: 20px;
                text-align: center;
                height: 300px;

                .wrapper-searchbox+img {
                    padding-top: 0;
                }
            }
        }
    }

    .customer-item-list {
        padding: 16px;
        margin-bottom: 24px;

        .list-img {
            margin-bottom: 16px;
        }

        .wrapper-img {
            min-width: 16px;
            margin-right: 8px;

            img {
                max-width: 16px;
            }
        }

        .view-more {
            position: relative;
            right: 0;
            bottom: 0;

            .btn {
                margin-top: 10px;
            }
        }
    }

    .list-tags {
        margin-bottom: 16px;

        li {
            margin-bottom: 10px;
        }
    }

    .customer-coupon {
        padding: 40px 0;

        .search-box {
            margin: 16px 0;
        }

        &-item {
            margin-bottom: 16px;
        }
    }

    .bp-breadcrumb {
        font-size: 20px;
        line-height: 24px;
    }

    .bp-tabs-right {
        padding: 0;
        margin-top: 0;
    }

    .bp {
        &-coupon {
            .search-box {
                margin-bottom: 24px;
            }

            &-detail {
                .detail-right {
                    .btn-getnow {
                        margin-top: 32px;
                    }
                }

                .description {
                    margin-top: 16px;
                    border: 2px solid $accent-primary;
                    padding: 16px 24px;
                }
            }

            &-list {
                padding: 0 8px;
            }
        }
    }

    .bm {
        &-plan-success {
            margin: 40px 0;
        }

        &-list {
            margin-top: 40px;
        }
    }

    .bm-modal-config {
        .modal-body {
            padding: 40px;
        }

        .apply-everyday,
        .text-config {
            margin-bottom: 30px;
        }

        .wrapper-config {
            margin: 30px 0;

            .form-group {
                label {
                    margin-bottom: 4px;
                }
            }
        }

        .wrapper-input {
            width: 250px;

            .square-input {
                width: 64px;
            }
        }

        .weekdays-selector input[type=checkbox]+label {
            margin-bottom: 10px;
        }
    }
}