.switch-btn {
    .checkbox {
        display: none;
    }

    .switch {
        align-items: center;
        border-radius: 500px;
        cursor: pointer;
        display: flex;
        height: 40px;
        justify-content: space-between;
        padding: 0 13px;
        position: relative;
        user-select: none;
        width: 105px;
        transition: all .2s ease;
    }

    .checkbox:checked~.switch {
        background-color: #fff;
        border: 2px solid $ic-primary;
    }

    .checkbox:not(:checked)~.switch {
        background-color: #fff;
        border: 2px solid $ic-primary;
    }

    &.disabled {
        cursor: not-allowed;

        label {
            cursor: not-allowed;
        }
        .checkbox:checked~.switch,
        .checkbox:not(:checked)~.switch {
            border: 2px solid $ic-disabled;
        }

        .switch-circle-inner {
            background-color: $ic-disabled;
        }

        .switch-left,
        .switch-right,
        .switch-text {
            color: $ic-disabled;
        }
    }

    .switch-left,
    .switch-right {
        color: $ic-primary;
        font-weight: bold;
        text-transform: uppercase;
        // font-size: 24px;
        // line-height: 28px;
    }

    .checkbox:checked~.switch .switch-left {
        visibility: hidden;
    }

    .checkbox:not(:checked)~.switch .switch-right {
        visibility: hidden;
    }

    .switch-circle {
        height: 40px;
        padding: 5px;
        position: absolute;
        transition: all 0.15s ease;
        width: 40px;
    }

    .checkbox:checked~.switch .switch-circle {
        left: 0;
        right: calc(100% - 40px);
    }

    .checkbox:not(:checked)~.switch .switch-circle {
        left: calc(100% - 40px);
        right: 0;
    }

    .switch-circle-inner {
        background-color: $ic-primary;
        border-radius: 50%;
        display: block;
        height: 100%;
        width: 100%;
    }

    .switch-text {
        margin-left: 10px;
        font-size: $fs24;
        line-height: 28px;
        // font-weight: 500;
    }
}

