// Custom select
.nice-select {
    font-size: 20px;
    border-radius: 0;
    transition: all .25s ease;
    padding-right: 36px;
    border-color: $text-color;

    .current {
        color: $secondary;
        font-size: $fs18;
    }

    &::after {
        border: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 8px solid $text-color;
        transform: none;
        transition: all .25s ease;
        width: 0;
        height: 0;
    }

    &.open::after {
        transform: none;
    }

    &.open, &:active, &:focus {
        border-color: $text-color;
    }
}

.nice-select .option {
    position: relative;
    padding: 12px;
    font-size: 18px;
    line-height: 21px;
}

.nice-select .option.selected.focus {
    background-color: $bg-secondary;
    color: $text-color;
    font-weight: 400;

    &::after {
        position: absolute;
        content: '';
        right: 12px;
        bottom: 16px;
        background-image: url('../images/ic-tick-selected.svg');
        width: 20px;
        height: 16px;
    }
}

.nice-select .list {
    width: 100%;
    border: 0;
    border-radius: 5px;
    box-shadow: 0px 2px 6px rgba(77, 78, 78, 0.2);
}

