.bootstrap-datetimepicker-widget {
    .table>:not(caption)>*>* {
        border: 0;
        box-shadow: none;
    }
    table {
        color: $text-color;
        border-spacing: 0;
        thead {
            tr {
                th {
                    border: 0;
    
                    &.picker-switch {
                        border: 1px solid $ic-primary;
                    }
                }
    
                &:nth-child(1) {
                    th {
                        padding: 12px 0; 
                        max-width: 46px;
                    }
                }
    
                &:nth-child(2) {
                    th {
                        padding: 12px 0; 
                        max-width: 46px;
                    }
                }
            }
        }
        tr {
            border: 0;
        }
    
        td {
            &.day {
                width: 46px;
                height: 46px;
                line-height: 46px;
                background: $white;
                border: 1px solid $secondary-invert;
                border-radius: 0;
                padding: 0;
                max-width: 46px;
                color: $text-color;
                position: relative;
                transition: all .2s ease;
    
                &:hover {
                    background: $bg-secondary;
                    color: $text-color;
                    // &::after {
                    //     position: absolute;
                    //     content: '';
                    //     width: 100%;
                    //     height: 100%;
                    //     border: 1px solid $accent-primary;
                    //     left: 0;
                    //     top: 0;
                    //     z-index: 2;
                    // }
                }
            }
    
            &.today {
                &::before {
                    display: none;
                }
            }

            &.active {
                background-color: $bg-secondary;
                color: $text-color;
                transition: all .2s ease;

                &::before {
                    display: none;
                }

                &::after {
                    position: absolute;
                    content: '';
                    width: 100%;
                    height: 100%;
                    border: 1px solid $accent-primary;
                    left: 0;
                    top: 0;
                    z-index: 2;
                }
            }

            &.new {
                background-color: $bg-secondary;
                color: $gray-100;
                cursor: not-allowed !important;

                &:hover {
                    color: $gray-100;
                }
            }
        }
    }
}


