body {
    font-family: 'Roboto', sans-serif;
    color: $text-color;
    font-size: 20px;
    line-height: 24px;
}

a,
a:hover {
    text-decoration: none;
    transition: all .2s ease;
}

.text-underline {
    text-decoration: underline;
}

.search-box {
    position: relative;

    .form-control {
        padding: 11px 48px;
    }

    &-icon,
    &-close {
        position: absolute;
        top: 9px;
    }

    &-icon {
        left: 16px;
    }

    &-close {
        right: 16px;
    }
}

// Page home
.navbar {
    border-bottom: 2px solid $accent-primary;
    padding: 14px 0;

    .nav-link {
        color: $secondary;
        font-weight: $fw-md;
        padding: 10px 25px !important;
        border-radius: 5px;
        font-size: $fs20;

        &:hover {
            background-color: rgba($accent-primary-hover, .1);
            color: $text-color;
        }
    }
}

.navbar-brand {
    img {
        height: 60px;
        width: 83px;
        object-fit: cover;
    }
}

.header-notification {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    background-color: $bg-secondary;
    @include flex();
    position: relative;
    cursor: pointer;

    .badge {
        position: absolute;
        border-radius: 50%;
        width: 14px;
        height: 14px;
        background: $accent-primary;
        color: $white;
        font-size: 8px;
        line-height: 8px;
        top: 15px;
        right: 15px;
        z-index: 2;
    }

    i {
        font-size: 30px;
    }
}

.wrapper-notification {
    position: relative;
    transition: all .3s ease;

    &:hover .wrapper-notify {
        opacity: 1;
        display: block;
        visibility: visible;
        transform: translateY(0);
    }
}

.wrapper-notify {
    position: absolute;
    width: 390px;
    right: 0;
    position: absolute;
    width: 390px;
    right: 0;
    z-index: 2;
    transition: all .3s ease;
    border-radius: 5px;
    opacity: 0;
    visibility: hidden;
    display: none;
    transform: translateY(50px);

    .spacer {
        height: 32px;
    }
}

.inner-notify {
    background: $bg-secondary;
    box-shadow: 0px 2px 6px rgba(77, 78, 78, 0.2);
    border-radius: 5px;
    padding: 2px 2px 0;
}

.notify {
    &-top {
        padding: 20px;

        span {
            font-size: 24px;
            font-weight: $fw-md;
        }

        a {
            text-decoration: underline;
            font-size: 16px;
            line-height: 18px;
        }
    }

    &-item {
        background: $white;
        margin-bottom: 5px;
        display: flex;
        align-items: center;
        position: relative;
        padding: 7px 30px;
        border-radius: 5px;

        &:hover {
            color: $text-color;
        }

        p {
            font-size: $fs16;
            line-height: 18px;
            margin-bottom: 6px;
        }

        small {
            font-size: 14px;
            line-height: 20px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &-badge {
        position: absolute;
        width: 16px;
        height: 16px;
        right: 20px;
        top: 32px;
    }

    &-img {
        width: 50px;
        min-width: 50px;
        height: 50px;
        border-radius: 100%;
        margin-right: 16px;

        img {
            width: 100%;
            height: 100%;
            border-radius: 100%;
        }
    }
}

.btn-dropdown {
    background: $bg-secondary;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    @include flex();
    margin-left: 10px;

    img {
        max-height: 7.5px;
    }
}

.dropdown-country {
    border: 1px solid $text-color;
    background: $white;
    position: relative;
    height: 64px;

    .dropdown-menu {
        width: 100%;
    }

    .dropdown-toggle {
        width: 100%;
        display: flex;
        align-items: center;
        line-height: 64px;
        padding-left: 12px;

        img {
            width: 24px;
            height: 18px;
            object-fit: cover;
        }

        &:hover {
            color: $text-color;
        }
    }

    .dropdown-toggle::before {
        display: none;
    }

    .dropdown-toggle::after {
        position: absolute;
        top: 28px;
        right: 12px;
        left: unset;
    }

    .selected {
        .dropdown-item {
            background-color: $bg-secondary;
            color: $text-color;
            position: relative;

            &::after {
                background-image: url('../images/ic-tick-selected.svg');
                background-repeat: no-repeat;
                background-position: right center;
                content: '';
                position: absolute;
                top: 24px;
                right: 12px;
                width: 20px;
                height: 16px;
            }
        }
    }

    .country-code {
        color: $secondary;
        margin-left: 8px;
    }
}

.dropdown-language {

    .dropdown-toggle::before,
    .dropdown-toggle::after {
        display: none;
    }

    .dropdown-menu[data-bs-popper] {
        margin-top: 16px;
    }

    .selected {
        .dropdown-item {
            background-color: $bg-secondary;
            color: $text-color;
            position: relative;

            &::after {
                background-image: url('../images/ic-tick-selected.svg');
                background-repeat: no-repeat;
                background-position: right center;
                content: '';
                position: absolute;
                top: 24px;
                right: 12px;
                width: 20px;
                height: 16px;
            }
        }
    }
}

.dropdown-user {
    a:hover {
        color: $text-color;
    }

    .avatar {
        margin-right: 10px;
        width: 60px;
        height: 60px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .drop-avatar {
        margin-right: 20px;
        width: 100px;
        height: 100px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .drop-user-info {
        .page-text-lg {
            margin-bottom: 18px;

            +span {
                border-bottom: 1px solid $text-color;
            }
        }
    }

    .dropdown-menu[data-bs-popper] {
        width: 436px;
        height: 368px;
        margin-left: -180px;
        margin-top: 40px;
        padding: 60px 64px 40px;

        .btn {
            max-width: 276px;
            margin: 0 auto;
        }

    }

    .user-info-btn {
        margin-top: 40px;

        .btn+.btn {
            margin-top: 20px;
        }
    }
}

.page-header {
    .btn-register {
        margin-right: 10px;
        padding: 0 18px;
    }

    .btn-login {
        min-width: 90px;
    }

    .wrapper-btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 60px;
        height: 60px;
        background: $bg-secondary;
        border-radius: 50%;
