.header-user-info {
    position: relative;

    .wraper-info {
        .avatar {
            width: 60px;
            height: 60px;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        span {
            margin: 0 10px;
        }
    }

    .wrapper-user-info {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
    }
}

// Page sign up
.page-signup {
