        .form-bill {
            background-color: $bg-secondary;
            padding: 100px 100px 86px;
            margin-bottom: 40px;
            border-radius: 5px;
        }

        .text-bill {
            margin-bottom: 40px;
        }

        .summary-bill {
            &-wrap {
                border-radius: 5px;
                border: 1px solid $accent-primary;
                background-color: $accent-secondary;
                padding: 40px;
                margin: 60px 0;
            }

            &-item {
                &:not(:last-child) {
                    margin-bottom: 40px;
                }
            }
        }

        .actions-btn {
            .btn {
                min-width: 234px;

                + .btn {
                    min-width: 102px;
                    margin-left: 10px;
                }
            }
        }
    }

    &-reservation {
        padding-left: 40px;
        .reservation-table {
            margin-top: 40px;
