.drop-zone {
    max-width: 400px;
    height: 400px;
    margin: 0 auto;
    cursor: pointer;
    background-color: $bg-secondary;

    &-edit {
        background-image: url('../images/bm-event-sample-photo.png');
        background-repeat: no-repeat;
    }
}

.drop-zone__prompt {
    width: 100%;
    height: 100%;
    @include flex;
    flex-direction: column;
    text-align: center;

    p > span {
        border-bottom: 1px solid;
    }
}

.drop-zone--over {
    border: 2px solid $accent-primary;
    border-radius: 10px;
}

.drop-zone__input {
    display: none;
}

.drop-zone__thumb {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    background-color: #cccccc;
    background-size: cover;
    position: relative;
}

.drop-zone__other {
    margin-top: 20px;
    text-align: center;
    cursor: pointer;

    img {
        margin-right: 10px;
    }

    span {
        border-bottom: 1px solid;
    }
}

