        height: 0;
      }
      
      .weekdays-selector input[type=checkbox] + label {
        display: inline-block;
        width: 100px;
        height: 86px;
        line-height: 86px;
        border: 1px solid $accent-primary;
        background: $accent-secondary;
        text-align: center;
        cursor: pointer;
        color: $text-color;
        transition: all .25s ease;
        font-weight: 500;

        &:hover {
